import * as Yup from 'yup'

export const validationSchema = Yup.object().shape({
    nome_usuario: Yup.string().required("Campo Obrigatrio"),
    senha_usuario: Yup.string().required("Campo Obrigatrio"),
});

export const validationSchemaPed = Yup.object().shape({
    capa: Yup.string().required("Capa do Pedido  obrigatrio"),
    cliente: Yup.string().required("Cliente  obrigatrio"),
    endereco_entrega: Yup.string().required("Endereo  obrigatrio"),
    forma_pagamento: Yup.string().required('Forma de pagamento obrigatria'),
    observacoes: Yup.string().notRequired(),
})

export const validationSchemaItens = Yup.object().shape({
    produto: Yup.string().required("Produto  obrigatrio"),
    quantidade: Yup.number().required("Quantidade  obrigatria").positive("A quantidade deve ser maior que zero"),
    preco: Yup.number().required("Preo  obrigatrio").positive("O preo deve ser maior que zero"),
    total: Yup.number().required("Total  obrigatrio").positive("O total deve ser maior que zero"),
})




